import common from '../../common/common.js'
import dateTimePicker from '../../common/dateTimePicker.js'
import util from '../../common/util.js'
import uniPopup from '../../components/uni-popup.vue';

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userInfo:[],
			
			id:0,
			goods:[],
			guige:'',
			guige_key:-1,
			total_amount:0,
			nums:1,
			
			nickname:'',		//姓名
			mobile:'',			//手机
			content:'',			//备注
							
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			
			info:{
				bg_color:'255,255,255',
				bg_alpha:1,
				delta:2,
				icon_color:'undefined'
			},
		};
	},
	
	onLoad(option) {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');			
		if(userInfo.users_id > 0){
			that.userInfo = userInfo
		}else{
			that.checkLogin();
			return false;
		}
		var id = option.id
		var guige_key = option.guige_key
		//id = 2;
		//guige_key = 1;
		that.id = id
		that.guige_key = guige_key
		
		console.log('titleHeight',common.titleHeight)
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;			
		
		that.getGoodsInfo();
		
	},
	
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		//console.log('address',common.address)
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		that.userid = userid
		that.userInfo = userInfo
		
		that.getGoodsInfo();
					
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	
	methods: {
		setNickname(e){
			var that = this
			var nickname = e.detail.value
			that.nickname = nickname
		},
		setMobile(e){
			var that = this
			var mobile = e.detail.value
			that.mobile = mobile
		},
		setContent(e){
			var that = this
			var content = e.detail.value
			that.content = content
		},
		//下单
		checkOrder(){
			var that = this
			var nickname = that.nickname ? that.nickname : that.userInfo.users_nickname;
			var mobile = that.mobile ? that.mobile : that.userInfo.users_mobile;
			var content = that.content;
			
			
			console.log("nickname",nickname)
			console.log("mobile",mobile)
			console.log("content",content)
			
			if(nickname == ''){
				uni.showToast({
					title: '请输入姓名',
					duration:1500,
					icon:'none'
				});
				return false
			}
			if(mobile == ''){
				uni.showToast({
					title: '请输入手机号码',
					duration:1500,
					icon:'none'
				});
				return false
			}
			var tjuid = uni.getStorageSync('tjuid');
			var guige = that.guige
			var guige_key = that.guige_key
			if(guige != '' && guige_key  > -1){
				var commission = guige[guige_key][4]
			}else{
				var commission = that.goods.goods_commission
			}
			
			uni.request({
				url: common.apiUrl + 'orders/checkSuperOrder',
				method: 'GET',
				data: {
					userid:that.userInfo.users_id,						
					goods_id:that.id,
					guige_key:that.guige_key,
					nums:that.nums,
					nickname:nickname,
					mobile:mobile,
					content:content,
					tjuid:tjuid,
					commission:commission,
				},
				success: res => {
					uni.showToast({
						title: '操作成功',
						duration: 1500,
						icon:'success'
					});
					var order_no = res.data.order_no;
					console.log('order_no1',order_no)
					//that.getUserInfo();
					
					uni.reLaunch({
						url: '/pages/orders/orders?id='+order_no+'&act=payfee&otype=super',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
				},
				fail: () => {},
				complete: () => {}
			});
			
		},
		//获取商品信息
		getGoodsInfo(){
			var that = this
			uni.request({
				url: common.apiUrl + 'shop/getSuperGoodsInfo',
				method: 'GET',
				data: {
					id:that.id
				},
				success: res => {
					var data = res.data
					that.goods = data.goods
					that.guige = data.guige
					that.stocks = res.data.goods.goods_stocks
					that.guige = res.data.guige
					that.picList = res.data.pics_list
					that.limitnums = res.data.goods.goods_maxnums
					that.canbuy = res.data.canbuy
					that.online = res.data.online
					
					that.updateAmount();
					
					uni.setNavigationBarTitle({
					    title: data.goods.goods_title
					});










				},
				fail: () => {},
				complete: () => {}
			});
		},
		//更新总金额
		updateAmount(){
			var that = this
			var total_amount = that.total_amount
			var nums = that.nums
			var guige = that.guige
			var guige_key = that.guige_key
			if(guige != ''){
				var price = guige[guige_key][1]
			}else{
				var price = that.goods.goods_price
			}
			total_amount = nums*price;
			that.total_amount = total_amount
		},
		
		//增加数量
		addNums(){
			var that = this
			var goods_stocks = that.goods.goods_stocks
			var nums = that.nums
			if(nums < goods_stocks){
				that.nums = nums+1
				that.updateAmount();
			}
		},
		//减少数量
		subNums(){
			var that = this
			var goods_stocks = that.goods.goods_stocks
			var nums = that.nums
			if(nums > 1){
				that.nums = nums-1
				that.updateAmount();
			}
		},
		
		
		//获取用户信息
		getUserInfo(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var userid = userInfo.users_id
			if(userid > 0){
				uni.request({
					url: common.apiUrl+'user/getUserInfo',
					method: 'GET',
					data: {
						userid:userid
					},
					success: res => {
						if(res.data.code == 0){
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo',userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if(res.confirm){
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		
		//用户登录
		checkLogin(){
			var that = this
			var userid = uni.getStorageSync('userid');
			if(!userid){
				console.log('还没有登录');
				uni.navigateTo({
					url: '/pages/userlogin/userlogin',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}else{
				console.log('已经登录');
			}
		},
	},
	
}
